<template>
  <div>
    <b-row class="mt-4">
      <b-col lg="12">
        <h5 class="bottom-border">最近新書</h5>
      </b-col>
    </b-row>

    <div data-aos="fade-up" data-aos-duration="1500">
      <b-row class="mt-3">
        <b-col lg="12">
          <splide :slides="getNewReleases" :options="options">
            <splide-slide v-for="slide in getNewReleases" :key="slide.index">
              <router-link :to="'/product/' + slide.isbn">
                <img :src="slide.img" />
              </router-link>
            </splide-slide>
          </splide>
        </b-col>
      </b-row>
    </div>

    <div data-aos="zoom-in-down" data-aos-duration="1500">
      <b-row class="mt-5">
        <b-tabs content-class="mt-3" justified>
          <b-tab
            v-for="(tab, index) in tabTiles"
            v-bind:key="index"
            :title="tab"
          >
            <b-card-group deck>
              <b-card
                v-for="item in [
                  getWeeklyHot,
                  getMonthlyHot,
                  getSeasonalHot,
                  getYearlyHot,
                ][index]"
                v-bind:key="item.index"
                border-variant="white"
              >
                <router-link
                  class="text-decoration-none"
                  :to="'/product/' + item.isbn"
                >
                  <b-card-img :src="item.img" :alt="item.isbn" top></b-card-img>
                </router-link>
                <b-card-text>
                  <router-link
                    class="text-decoration-none"
                    :to="'/product/' + item.isbn"
                  >
                    <small class="text-muted">{{ item.title }}</small>
                  </router-link>
                </b-card-text>
              </b-card>
            </b-card-group>
          </b-tab>
        </b-tabs>
      </b-row>
    </div>
    <!-- <div data-aos="fade-up">
      <h1>Hello Dev Community!</h1>
    </div> -->
  </div>
</template>

<script>
// import TreeMenu from "@/components/TreeMenu.vue";
import MyFooter from "@/components/MyFooter.vue";
import MyHeader from "@/components/MyHeader.vue";
// import home from "@/api/home";
import config from "@/common/config";
import SliderSecond from "@/components/SliderSecond.vue";
import SliderThird from "@/components/SliderThird.vue";
import store from "@/store";
import { mapGetters } from "vuex";

/*
How to bind img src to data in Vue
https://stackoverflow.com/questions/48847644/how-to-bind-img-src-to-data-in-vue

Vue-cli compile image src via dynamic property #450
https://github.com/vuejs-templates/webpack/issues/450

https://vuejs-templates.github.io/webpack/static.html
https://webpack.js.org/guides/dependency-management/#require-with-expression

bootstrap-vue Component img src resolving
https://bootstrap-vue.org/docs/reference/images
*/

// let hotProducts = [
//   home.weeklyHot,
//   home.monthlyHot,
//   home.seasonalHot,
//   home.yearlyHot,
// ];

export default {
  name: "Index",
  data() {
    return {
      slide: 0,
      sliding: null,
      //list: config.treeMenu,
      loading: false,
      banner: config.banner,
      bannerDimension: "height:180px;",
      myDimension: "height:500px;max-width:100%;",
      tinyML: require("@/assets/img/TinyML.webp"),
      tabTiles: config.tabTiles,
      // hotProducts: hotProducts,
      // weeklyHot: home.weeklyHot,
      // monthlyHot: home.monthlyHot,
      // seasonalHot: home.seasonalHot,
      // yearlyHot: home.yearlyHot,
      options: {
        // type: 'slide',
        type: "loop",
        arrows: true,
        // arrows: 'slider',
        autoplay: true,
        interval: 2000,
        trimSpace: true,
        pagination: true,
        rewind: true,
        autoWidth: true,
        //width: 1100,
        perPage: 5,
        perMove: 1,
        gap: "1rem",
        // focus: "center",
        focus: 0,
      },
      // slides: home.newBookReleases,
    };
  },
  beforeCreate() {
    //this.$store.dispatch("cart/refresh");
  },
  // created() {
  //   Promise.all([
  //     this.$store.dispatch("products/getNewBookReleases"),
  //     this.$store.dispatch("products/getHotSale"),
  //   ])
  //     .then((values) => {
  //       console.log("Index.vue NewBookReleases", JSON.stringify(values[0]));
  //       console.log("Index.vue HotSale", JSON.stringify(values[1]));
  //     })
  //     .catch((reason) => {
  //       console.log("Index.vue reason", reason);
  //       // alert(reason);
  //       this.$store.dispatch("modal/setModalMsg", reason);
  //       this.$store.dispatch("modal/setModalShow", true);
  //     });
  // },
  beforeRouteEnter(to, from, next) {
    Promise.all([
      store.dispatch("products/getNewBookReleases"),
      store.dispatch("products/getHotSale"),
    ])
      .then((values) => {
        console.log("Index.vue NewBookReleases", JSON.stringify(values[0]));
        console.log("Index.vue HotSale", JSON.stringify(values[1]));
        next();
      })
      .catch((reason) => {
        console.log("Index.vue reason", reason);
        alert(reason);
        // store.dispatch("modal/setModalMsg", reason);
        // store.dispatch("modal/setModalShow", true);
      });
  },
  computed: {
    ...mapGetters("products", [
      "getNewReleases",
      "getWeeklyHot",
      "getMonthlyHot",
      "getSeasonalHot",
      "getYearlyHot",
    ]),
  },
  methods: {
    handleScroll() {
      let scrollTop =
        document.documentElement.scrollTop ||
        window.pageYOffset ||
        document.body.scrollTop;
      console.log(scrollTop);
      console.log(
        "document.documentElement.scrollTop: " +
          document.documentElement.scrollTop
      );
      console.log("window.pageYOffset: " + window.pageYOffset);
      console.log("document.body.scrollTop: " + document.body.scrollTop);

      let windowHeight = window.innerHeight;
      console.log("windowHeight: " + windowHeight);
      console.log("document.body.clientHeight: " + document.body.clientHeight);
      if (scrollTop + windowHeight + 250 >= document.body.clientHeight) {
        this.loading = true;
        console.log("this.loading: " + this.loading);
      } else {
        this.loading = false;
      }
    },
  },
  components: {
    //DropDownMenu,
    MyHeader,
    //TreeMenu,
    MyFooter,
    SliderSecond,
    SliderThird,
  },
  mounted() {
    //window.addEventListener("scroll", this.handleScroll, true);
  },
  destroyed() {
    //window.removeEventListener("scroll", this.handleScroll, true);
  },
};
</script>

